<template>
<div class="con-inner">
    <div class="top">
        <div class="tit-area">
            <span class="logo sisterhood">
                <span class="blind">    </span>
            </span>
            <div class="tit-txt">
                <h1></h1>
                <p class="author"></p>
            </div>
        </div>
        <div class="season-ctrl">
            <div class="button-area">
                <button type="button" class="button is-primary" @click="sortSeason('all')"></button>
                <button type="button" class="button is-primary" @click="sortSeason(1)">1</button>
                <button type="button" class="button is-primary" @click="sortSeason(2)">2</button>
                <button type="button" class="button is-primary" @click="sortSeason(3)">3</button>
                <button type="button" class="button is-primary" @click="sortSeason(4)">4</button>
            </div>
            <ul class="link-list">
                <li v-if="season !== 1">
                    <a href="https://www.podbbang.com/channels/1771727" target="_blank" title=" "><span class="icon is-small"><i class="mdi mdi-podcast"></i></span></a>
                </li>
                <li v-if="!(season == 1 || season == 4)">
                    <a href="https://podcasts.apple.com/us/podcast/%EC%8B%9C%EC%8A%A4%ED%84%B0%ED%9B%84%EB%93%9C/id1462838595" target="_blank" title=" "><span class="icon is-small"><i class="mdi mdi-podcast"></i></span> </a>
                </li>
                <li v-if="season !== 4">
                    <a href="https://audioclip.naver.com/channels/1111" target="_blank" title=" "><span class="icon is-small"><i class="mdi mdi-podcast"></i></span>  </a>
                </li>
            </ul>
        </div> 
    </div>   
    <div class="form-ctrl">
        <div class="ctrl-btn">
            <button type="button" class="button is-primary" @click="sortCtrl">
                <span class="icon is-small"><i class="mdi mdi-sort"></i></span>  
                <span class="tx">{{ sortTx }}</span>  
            </button>
            <button type="button" class="button is-primary" id="btnFilter" @click="setFilterActive">
                <span class="icon is-small"><i class="mdi" v-bind:class="[filterActive ? 'mdi-filter' : 'mdi-filter-outline']"></i></span>  
                <span class="blind"></span>  
            </button>
        </div>
        <div class="input-search">
            <select id="searchOption" @change="setSearchOption($event)">
                <option value="mainTit"> </option>
                <option value="subTit">  </option>
            </select>
            <input type="text" v-model="search">
        </div>
        <p class="notice">*         .</p>
    </div>
    <div class="list">
        <h2 class="blind"> </h2>
         <div class="collapse card" v-for="(episode, idx) in filteredData" :key="idx" :id="'episode' + idx">
             <episode-item :episode="episode"/>
        </div>
    </div>
</div>
</template>

<script src="../../assets/js/sisterhood.js"></script>